.editor {
    width: 100%;
    background: #FAFAFA;
   
}

.editor :global(.demo-wrapper) {
    padding: 0;
    padding-bottom: 20px;
    border-radius: 8px;
}

.editor :global(.rdw-editor-toolbar) {
    padding: 16px;
}

.editor :global(.rdw-editor-main) {
    padding: 0 20px;
}
.editor :global(.rdw-editor-toolbar .rdw-option-wrapper){
    border: none;
}