.select{
    height: 100% !important;
  }
  .select :global(.MuiSelect-icon) {
    top: 15px;
    left: 7px;
    right: unset;
  }
  .select :global(.MuiSelect-icon path) {
    fill: #242424;
  }
  @media(max-width:768px){
    .select :global(.MuiSelect-icon) {
      top: 10px;
    }
  }