.content::-webkit-scrollbar {
  display: none;
}

.back_drop {
  opacity: 0.5;
}

.arrow_con {
  height: 1.5rem;
  width: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #02466A1A;
}

.icons {
  width: 24px;
  height: 24px;
}

.icons path {
  fill: #1DBBBE;
}

.editor {
  width: 752px;
  max-width: 100%;
  background: #FFFFFF;
  box-shadow: 0px 3px 6px #0000000F;
  border-radius: 8px;
}

.editor :global(.demo-wrapper) {
  padding: 0;
  padding-bottom: 20px;
  border-radius: 8px;
}

.editor :global(.demo-wrapper .rdw-editor-toolbar) {
  padding: 20px;
  border: 1px solid #67747B33;
  border-radius: 8px;
}

.editor :global(.demo-wrapper .rdw-editor-main) {
  padding: 0 20px;
}

.editor :global(.rdw-editor-toolbar .rdw-option-wrapper) {
  border: none;
}

@media(max-width:768px){
  .container{
    height: 100% !important;
  }
}